import React from 'react';
import { useQuery } from 'react-query';
import { Typography, makeStyles, Container } from '@material-ui/core';
import ProductCard from './productCard';

import getProductByCategory from '../services/products/getProductByCategory';
import Loading from './loading';
import { Link } from 'react-router-dom';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import Slider from 'react-slick';

const useStyles = makeStyles((theme) => ({
  newArrivals_container: {
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    marginTop: '1rem',
    backgroundImage: `url(./back.jpg)`,
    paddingBottom: '1rem',
  },
  newArrivals_items: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '2rem 0',
    justifyContent: 'space-evenly',

    '& > *': {
      margin: '10px',
    },
  },
  sliderContainer: {
    maxWidth: '90%',
    marginLeft: '4rem',
    [theme.breakpoints.down('sm')]: {
      marginLeft: '1rem',
    },
  },
}));

const CardContainerWithBackgroundImage = ({ category, noBGImage }) => {
  const classes = useStyles();

  const { isLoading, isError, data, error } = useQuery(
    ['category', category.id],
    () => getProductByCategory(category.id)
  );

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return <h1>{error}</h1>;
  }

  if (data && data.length === 0) {
    return (
      <Container>
        <Typography variant="h5">No products currently</Typography>
      </Container>
    );
  }

  const SampleNextArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowForward
          size={30}
          style={{
            color: '#000',
            background: '#FFFFFF',
            border: '1px solid #000',
            borderRadius: '50%',
            padding: '4px',
          }}
        />
      </div>
    );
  };

  const SamplePrevArrow = (props) => {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <ArrowBack
          size={30}
          style={{
            color: '#000',
            background: '#FFFFFF',
            border: '1px solid #000',
            borderRadius: '50%',
            padding: '4px',
          }}
        />
      </div>
    );
  };

  const settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    centerMode: false,
    speed: 1000,
    autoplaySpeed: 3000,
    slidesToShow: 3,
    slidesToScroll: 2,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    responsive: [
      {
        breakpoint: 1080,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 700,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div style={{ marginBottom: '2rem' }}>
      <Link to={`/category/${category.id}`}>
        <Typography
          color="textPrimary"
          variant="h5"
          gutterBottom
          align="center"
          style={{
            fontWeight: 'bold',
            marginTop: '1rem',
          }}
        >
          <span
            style={{
              border: '1px solid #000',
              borderRadius: '4px',
              padding: '10px',
            }}
          >
            {/* {data[0].categorization[0].category_name.toUpperCase()} */}
            {category &&
              category.categories &&
              category.categories.toUpperCase()}
          </span>
        </Typography>
      </Link>
      <div className={noBGImage ? null : classes.newArrivals_container}>
        {data && data.length <= 2 ? (
          <div className={classes.newArrivals_items} align="middle">
            {data.map((product) => (
              <ProductCard
                key={product.id}
                name={product.product_name}
                subtitle={product.product_desc}
                image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                brandName={product.brand && product.brand.brand_name}
                price={product.price}
                id={product.id}
                discount={product.discount_amount}
              />
            ))}
          </div>
        ) : (
          <div className={classes.sliderContainer} align="middle">
            <Slider {...settings}>
              {data &&
                data.map((product) => (
                  <ProductCard
                    key={product.id}
                    name={product.product_name}
                    subtitle={product.product_desc}
                    image={`${process.env.REACT_APP_UPLOAD_URL}${product.images[0].image}`}
                    brandName={product.brand && product.brand.brand_name}
                    price={product.price}
                    id={product.id}
                    discount={product.discount_amount}
                  />
                ))}
            </Slider>
          </div>
        )}
      </div>
    </div>
  );
};

export default CardContainerWithBackgroundImage;
