import React from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { makeStyles, Typography } from '@material-ui/core';
import { useQuery } from 'react-query';

import getProductById from '../services/products/getProductById';
import Loading from '../components/loading';
import ProductInfo from '../components/productInfo';
import QuestionAnswer from '../components/questionAnswer';
import CustomerReviews from '../components/customerReviews';

const useStyles = makeStyles((theme) => ({
  container: {
    margin: '3rem',
    [theme.breakpoints.down('xs')]: {
      margin: '3rem 1rem',
    },
  },
}));

const ProductDetails = () => {
  const { id } = useParams();
  const classes = useStyles();

  const history = useHistory();

  const { isLoading, isError, data, error } = useQuery(['product', id], () =>
    getProductById(id)
  );

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return <h1>{error}</h1>;
  }

  return (
    <div className={classes.container}>
      <Typography
        gutterBottom
        style={{
          margin: '2rem',
          cursor: 'pointer',
          fontWeight: 600,
        }}
        onClick={() => history.goBack()}
        color='secondary'
      >
        GO BACK
      </Typography>
      <ProductInfo
        images={data['Product Details'][0].images}
        details={data['Product Details'][0]}
        othersizes={data['Other Sizes']}
      />
      <div>
        <QuestionAnswer productId={data['Product Details'][0].id} />
        <CustomerReviews
          reviewCount={data['Product Details'][0].review_count}
          productId={data['Product Details'][0].id}
        />
      </div>
    </div>
  );
};

export default ProductDetails;
