import { useQuery } from 'react-query';
import ProductCaraousel from '../components/productCaraousel';
import NewArrivals from '../components/newArrivals';
import Testimonials from '../components/testimonials';
import getCategories from '../services/products/getCategories';
import SkinCareRoutines from '../components/skinCareRoutines';
import CardContainerWithBackgroundImage from '../components/cardsContainerWithBackgroundImage';
// import CardContainer from '../components/cardContainer';
import BrandImages from '../components/brandImages';
import Loading from '../components/loading';
import { Box, Container, makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  imageBox: {
    width: '25vw',
    [theme.breakpoints.down('xs')]: {
      width: '80vw',
      margin: 'auto',
    },
  },
}));

const Home = () => {
  const { data, isLoading } = useQuery('categories', () => getCategories());

  const classes = useStyles();

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div>
      <ProductCaraousel />
      <NewArrivals />
      {data &&
        data.length > 0 &&
        data.map((category, index) => {
          return index % 2 === 0 ? (
            <CardContainerWithBackgroundImage category={category} />
          ) : (
            <CardContainerWithBackgroundImage
              category={category}
              noBGImage={true}
            />
          );
        })}
      <SkinCareRoutines />
      <Testimonials />
      <BrandImages />
      <Container>
        <Box
          paddingLeft="2rem"
          display="flex"
          flexWrap="wrap"
          justifyContent="space-between"
          alignItems="center"
        >
          <Box className={classes.imageBox}>
            <a href="https://esewa.com.np/#/home" target="blank">
              <img src="/images/esewa.png" alt="esewa-kbs" />
            </a>
          </Box>
          <Box className={classes.imageBox}>
            <a href="https://rawasoftware.com/" target="blank">
              <img src="/images/rawasoft.png" alt="rawasoft" />
            </a>
          </Box>
          <Box className={classes.imageBox}>
            <a href="https://www.thegarudaexpress.com/" target="blank">
              <img src="/images/garuda.png" alt="garuda" />
            </a>
          </Box>
        </Box>
      </Container>
    </div>
  );
};

export default Home;
